import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import DateService from '../../core/date.service';
import BannerComponent from '../_reusable/banner/BannerComponent';
import TableComponent from '../_reusable/table/TableComponent';
import SubmissionDataComponent from './SubmissionDataComponent';
import SubmissionForms from './SubmissionForms';
import authService from '../../core/auth.service';
import SubmissionTableHeaderComponent from './table-header/SubmissionHeaderComponent';
import SubmissionService from './SubmissionService';

function SubmissionComponent() {
  const navigate = useNavigate();
  const tableHeaderColumns = SubmissionForms.tableHeaders;
  const [submissions, setSubmissions] = useState([]);
  const [paginationParameters, setPaginationParameters] = useState({});
  const [tableParameters, setTableParameters] = useState({
    pageNumber: 1,
    pageSize: 50,
  });
  const [filterModel, setFilterModel] = useState({});
  const [isLoading, setIsLoading] = useState(true);
  const [searchKey, setSearchKey] = useState();
  const [sortInfo, setSortInfo] = useState({});
  const [sortBy, setSortBy] = useState([
    {},
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: false,
    },
    {
      state: true,
    },
    {
      state: false,
    },
  ]);

  const constructQueryString = (filterModelData, tableParametersData) => {
    const channelFilterParam =
      filterModelData.channelFilter !== undefined
        ? `&channelFilter=${filterModelData.channelFilter}`
        : '';
    const channelFilterValueParam =
      filterModelData.channelFilterValue !== undefined
        ? `&channelFilterValue=${filterModelData.channelFilterValue}`
        : '';
    const periodFilterParam =
      filterModelData.periodFilter !== undefined
        ? `&periodFilter=${filterModelData.periodFilter}`
        : '';
    const periodFilterStartRangeParam =
      filterModelData.periodFilterStartRange !== undefined
        ? `&periodFilterStartRange=${DateService.toIsoFormat(
            filterModelData.periodFilterStartRange
          )}`
        : '';
    const periodFilterEndRangeParam =
      filterModelData.periodFilterEndRange !== undefined
        ? `&periodFilterEndRange=${DateService.toIsoFormat(
            filterModelData.periodFilterEndRange
          )}`
        : '';
    const weightedFilterParam =
      filterModelData.weightedFilter !== undefined
        ? `&weightedFilter=${filterModelData.weightedFilter}`
        : '';
    const weightedFilterValueParam =
      filterModelData.weightedFilterValue !== undefined
        ? `&weightedFilterValue=${filterModelData.weightedFilterValue}`
        : '';
    const idFilterParam =
      filterModelData.idFilter !== undefined
        ? `&idFilter=${filterModelData.idFilter}`
        : '';
    const idFilterValueParam =
      filterModelData.idFilterValue !== undefined
        ? `&idFilterValue=${filterModelData.idFilterValue}`
        : '';

    const userSortParam =
      filterModelData.sort !== undefined ? `&sort=${filterModelData.sort}` : '';
    const sortByParam =
      filterModelData.sortBy !== undefined
        ? `&sortBy=${filterModelData.sortBy}`
        : '';
    const searchKeyParam =
      searchKey !== undefined ? `&SearchKey=${searchKey}` : '';
    const useCacheParam =
      filterModelData.useCache !== undefined
        ? `&useCache=${filterModelData.useCache}`
        : '';

    const queryString = `?pageNumber=${tableParametersData.pageNumber}&pageSize=${tableParametersData.pageSize}${channelFilterParam}${channelFilterValueParam}${periodFilterParam}${periodFilterStartRangeParam}${periodFilterEndRangeParam}${weightedFilterParam}${weightedFilterValueParam}${idFilterParam}${idFilterValueParam}${userSortParam}${sortByParam}${searchKeyParam}${useCacheParam}`;

    return queryString;
  };

  const handleSetSortBy = (id) => {
    const newArr = sortBy;

    if (newArr[id].state === true) {
      newArr[id].state = false;
    } else {
      newArr[id].state = true;
    }

    setSortBy(newArr);
    setSortInfo({
      sort: id,
      sortBy: newArr[id].state ? 1 : 2,
    });
  };

  const onClickedButtonFilter = (model) => {
    setFilterModel(model);
  };

  const onClickedButtonClearAllFilters = () => {
    setFilterModel({
      sort: sortInfo.sort,
      sortBy: sortInfo.sortBy,
      useCache: true,
    });
  };

  const getSubmissions = () => {
    setIsLoading(true);
    SubmissionService.getSubmissions(
      constructQueryString(filterModel, tableParameters)
    ).then((response) => {
      setPaginationParameters({
        totalCount: response.data.totalCount,
        pageSize: response.data.pageSize,
        currentPage: response.data.currentPage,
        totalPages: response.data.totalPages,
      });
      setSubmissions(response.data.submissions);
      setIsLoading(false);
    });
  };

  const handleSearch = (searchVal) => {
    setSearchKey(searchVal);
  };

  const paginate = (pageNumber, size) => {
    setTableParameters({ pageNumber, pageSize: size });
  };

  const handleSort = (id) => {
    const newArr = sortBy;

    if (newArr[id].state === true) {
      newArr[id].state = false;
    } else {
      newArr[id].state = true;
    }

    setSortBy(newArr);
    setSortInfo({
      organisationSort: id,
      sortBy: newArr[id].state ? 1 : 2,
    });
  };

  const callbackSelectSubmissions = (data) => {
    authService.setSelectedSubmission({ data });
    navigate(`${process.env.REACT_APP_SUBFOLDER}/submission-scheduler`, {
      state: { data },
    });
  };

  useEffect(() => {
    constructQueryString(filterModel, tableParameters);
    getSubmissions();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tableParameters, filterModel, searchKey]);

  return (
    <div>
      <BannerComponent
        componentName="Submissions"
        addButtonText="New"
        isAddButtonHidden
        //  callbackAddButtonFunction={openAddPanel}
        handleSearch={handleSearch}
      />

      <hr style={{ background: '#c8c8c8' }} />

      <div className="d-flex">
        <div style={{ width: '100%' }}>
          <TableComponent
            component={
              <SubmissionDataComponent
                submissions={submissions}
                callbackSelectSubmissions={callbackSelectSubmissions}
                isLoading={isLoading}
              />
            }
            tableHeaderColumns={tableHeaderColumns}
            paginationParameters={paginationParameters}
            paginate={paginate}
            // onExport={handleExport}
            sortHeader={handleSort}
            isExporting={false}
            hasExport={false}
            isTableBodyLoading={isLoading}
            tableHeader={
              <SubmissionTableHeaderComponent
                onClickedButtonClearAllFilters={onClickedButtonClearAllFilters}
                filterModel={filterModel}
                sortBy={sortBy}
                handleSetSortBy={handleSetSortBy}
                onClickedButtonFilter={onClickedButtonFilter}
              />
            }
          />
        </div>
      </div>
    </div>
  );
}

export default SubmissionComponent;
