import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from './SubmissionTableHeader.module.css';
import filterIcon from '../../../assets/icons/Filter.png';
import filteredIcon from '../../../assets/icons/Filtered.png';
import arrowUpIcon from '../../../assets/icons/Arrow-Up.png';
import clearFilterIcon from '../../../assets/icons/Clear-Filter.svg';

import SubmissionForms from '../SubmissionForms';

import StartsWithFilterComponent from './filters/starts-with-filter/starts.with.filter.component';
import NumberFilterComponent from './filters/number-filter/number.filter.component';
import DateFilterComponent from './filters/date-filter/date.filter.component';

function SubmissionTableHeaderComponent(props) {
  const {
    sortBy,
    filterModel,
    onClickedButtonFilter,
    handleSetSortBy,
    onClickedButtonClearAllFilters,
    onClickedButtonClearFilter,
  } = props;
  const { tableHeaders } = SubmissionForms;
  const [showChannelFilter, setShowChannelFilter] = useState(false);
  const [showPeriodFilter, setShowPeriodFilter] = useState(false);
  const [showWeightedFilter, setShowWeightedFilter] = useState(false);
  const [showIDFilter, setShowIDFilter] = useState(false);

  const [showClearFilterIcon, setShowClearAllFilterIcon] = useState(false);
  const channelFilterRef = useRef();
  const periodFilterRef = useRef();
  const weightedFilterRef = useRef();
  const idFilterRef = useRef();

  const toggleDropdown = (header) => {
    if (header === tableHeaders[0]) {
      setShowChannelFilter(!showChannelFilter);
    } else {
      setShowChannelFilter(false);
    }

    if (header === tableHeaders[1]) {
      setShowPeriodFilter(!showPeriodFilter);
    } else {
      setShowPeriodFilter(false);
    }

    if (header === tableHeaders[2]) {
      setShowWeightedFilter(!showWeightedFilter);
    } else {
      setShowWeightedFilter(false);
    }

    if (header === tableHeaders[3]) {
      setShowIDFilter(!showIDFilter);
    } else {
      setShowIDFilter(false);
    }
  };

  const backdropClicked = (header) => {
    switch (header) {
      case tableHeaders[0]:
        setShowChannelFilter(false);
        break;
      case tableHeaders[1]:
        setShowPeriodFilter(false);
        break;
      case tableHeaders[2]:
        setShowWeightedFilter(false);
        break;
      case tableHeaders[3]:
        setShowIDFilter(false);
        break;
      default:
        break;
    }
  };

  const onClickedButtonFilterHeader = (model) => {
    onClickedButtonFilter(model);
  };

  const handleSort = (id) => {
    const model = JSON.parse(JSON.stringify(filterModel));

    model.sort = id;
    model.sortBy = sortBy[id].state ? 2 : 1;

    onClickedButtonFilterHeader(model);
    handleSetSortBy(id);
  };

  const onClickedButtonClearAllFiltersHeader = () => {
    channelFilterRef.current.clearFilterModel();
    periodFilterRef.current.clearFilterModel();
    weightedFilterRef.current.clearFilterModel();
    idFilterRef.current.clearFilterModel();

    onClickedButtonClearAllFilters();
  };

  const handleClearFilterIcon = () => {
    setShowClearAllFilterIcon(
      !!(
        filterModel.channelFilter
        || filterModel.channelFilterValue
        || filterModel.periodFilter
        || filterModel.periodFilterStartRange
        || filterModel.periodFilterEndRange
        || filterModel.weightedFilter
        || filterModel.weightedFilterValue
        || filterModel.idFilter
        || filterModel.idFilterValue
      ),
    );
  };

  useEffect(() => {
    handleClearFilterIcon();

    // eslint-disable-next-line
  }, [filterModel]);

  return (
    <tr>
      <td className={styles.columns}>
        <span
          onClick={() => handleSort(1)}
          onKeyDown={() => handleSort(1)}
          role="presentation"
        >
          {tableHeaders[0]}
        </span>
        {' '}
        <img
          className={`${styles.filterIcons} mx-2`}
          src={
            filterModel.channelFilter && filterModel.channelFilterValue
              ? filteredIcon
              : filterIcon
          }
          alt="FilterIcon1"
          onClick={() => toggleDropdown(`${tableHeaders[0]}`)}
          onKeyDown={() => toggleDropdown(`${tableHeaders[0]}`)}
          role="presentation"
        />
        <div
          className={styles.dropdownFilter}
          style={{ display: showChannelFilter ? null : 'none' }}
        >
          <StartsWithFilterComponent
            ref={channelFilterRef}
            component={tableHeaders[0]}
            filterModel={filterModel}
            onClickedButtonFilter={onClickedButtonFilterHeader}
            onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[0]}`)}
            toggleDropdown={toggleDropdown}
            backdropClicked={backdropClicked}
          />
        </div>
        <img
          src={arrowUpIcon}
          alt="ArrowUpIcon6"
          style={{
            display: filterModel.sort === 1 ? null : 'none',
            transform: sortBy[1].state ? 'rotate(0deg)' : 'rotate(180deg)',
          }}
        />
      </td>

      <td className={styles.columns}>
        <span
          onClick={() => handleSort(2)}
          onKeyDown={() => handleSort(2)}
          role="presentation"
        >
          {tableHeaders[1]}
        </span>
        {' '}
        <img
          className={`${styles.filterIcons} mx-2`}
          src={
            filterModel.periodFilter
            && filterModel.periodFilterStartRange
            && filterModel.periodFilterEndRange
              ? filteredIcon
              : filterIcon
          }
          alt="FilterIcon2"
          onClick={() => toggleDropdown(`${tableHeaders[1]}`)}
          onKeyDown={() => toggleDropdown(`${tableHeaders[1]}`)}
          role="presentation"
        />
        <div
          className={styles.dropdownValidityDate}
          style={{ display: showPeriodFilter ? null : 'none' }}
        >
          <DateFilterComponent
            ref={periodFilterRef}
            component={tableHeaders[1]}
            filterModel={filterModel}
            onClickedButtonFilter={onClickedButtonFilterHeader}
            onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[1]}`)}
            toggleDropdown={toggleDropdown}
            backdropClicked={backdropClicked}
          />
        </div>
        <img
          src={arrowUpIcon}
          alt="ArrowUpIcon2"
          style={{
            display:
              filterModel.sort === 2 || filterModel.sort === undefined
                ? null
                : 'none',
            transform: sortBy[2].state ? 'rotate(0deg)' : 'rotate(180deg)',
          }}
        />
      </td>

      <td className={styles.columns}>
        <span
          onClick={() => handleSort(3)}
          onKeyDown={() => handleSort(3)}
          role="presentation"
        >
          {tableHeaders[2]}
        </span>
        {' '}
        <img
          className={`${styles.filterIcons} mx-2`}
          src={
            filterModel.weightedFilter && filterModel.weightedFilterValue
              ? filteredIcon
              : filterIcon
          }
          alt="FilterIcon3"
          onClick={() => toggleDropdown(`${tableHeaders[2]}`)}
          onKeyDown={() => toggleDropdown(`${tableHeaders[2]}`)}
          role="presentation"
        />
        <div
          className={styles.dropdownFilter}
          style={{ display: showWeightedFilter ? null : 'none' }}
        >
          <NumberFilterComponent
            ref={weightedFilterRef}
            component={tableHeaders[2]}
            filterModel={filterModel}
            onClickedButtonFilter={onClickedButtonFilterHeader}
            onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[2]}`)}
            toggleDropdown={toggleDropdown}
            backdropClicked={backdropClicked}
          />
        </div>
        <img
          src={arrowUpIcon}
          alt="ArrowUpIcon3"
          style={{
            display: filterModel.sort === 3 ? null : 'none',
            transform: sortBy[3].state ? 'rotate(0deg)' : 'rotate(180deg)',
          }}
        />
      </td>

      <td className={styles.columns}>
        <span
          onClick={() => handleSort(4)}
          onKeyDown={() => handleSort(4)}
          role="presentation"
        >
          {tableHeaders[3]}
        </span>
        {' '}
        <img
          className={`${styles.filterIcons} mx-2`}
          src={
            filterModel.idFilter && filterModel.idFilterValue
              ? filteredIcon
              : filterIcon
          }
          alt="FilterIcon3"
          onClick={() => toggleDropdown(`${tableHeaders[3]}`)}
          onKeyDown={() => toggleDropdown(`${tableHeaders[3]}`)}
          role="presentation"
        />
        <div
          className={styles.dropdownFilter}
          style={{ display: showIDFilter ? null : 'none' }}
        >
          <NumberFilterComponent
            ref={idFilterRef}
            component={tableHeaders[3]}
            filterModel={filterModel}
            onClickedButtonFilter={onClickedButtonFilterHeader}
            onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[3]}`)}
            toggleDropdown={toggleDropdown}
            backdropClicked={backdropClicked}
          />
        </div>
        <img
          src={arrowUpIcon}
          alt="ArrowUpIcon4"
          style={{
            display: filterModel.sort === 4 ? null : 'none',
            transform: sortBy[4].state ? 'rotate(0deg)' : 'rotate(180deg)',
          }}
        />
      </td>

      <td className={styles.columns}>
        <span>{tableHeaders[4]}</span>
        {' '}
        <img
          className={`${styles.clearFilterIcons}`}
          style={{ display: showClearFilterIcon ? null : 'none' }}
          src={clearFilterIcon}
          alt="ClearFilterIcon"
          onClick={onClickedButtonClearAllFiltersHeader}
          onKeyDown={onClickedButtonClearAllFiltersHeader}
          role="presentation"
        />
      </td>
    </tr>
  );
}

SubmissionTableHeaderComponent.propTypes = {
  sortBy: PropTypes.arrayOf(PropTypes.shape({ state: PropTypes.bool }))
    .isRequired,
  filterModel: PropTypes.shape.isRequired,
  onClickedButtonFilter: PropTypes.func.isRequired,
  handleSetSortBy: PropTypes.func.isRequired,
  onClickedButtonClearAllFilters: PropTypes.func.isRequired,
  onClickedButtonClearFilter: PropTypes.func.isRequired,
};
export default SubmissionTableHeaderComponent;
