import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import styles from './number.filter.module.css';

const { forwardRef, useImperativeHandle } = React;
const NumberFilterComponent = forwardRef((props, ref) => {
  const options = [
    {
      optionId: 1,
      optionText: 'Equal',
    },
    {
      optionId: 2,
      optionText: 'Greater Than',
    },
    {
      optionId: 3,
      optionText: 'Greater Than or Equal',
    },
    {
      optionId: 4,
      optionText: 'Less Than',
    },
    {
      optionId: 5,
      optionText: 'Less Than or Equal',
    },

    {
      optionId: 6,
      optionText: 'Not Equal',
    },
  ];

  const [selectedOption, setSelectedOption] = useState(options[0]);
  const [showOptions, setShowOptions] = useState(false);
  const [numberInput, setNumberInput] = useState('');
  const [disableFilterButton, setDisableFilterButton] = useState(true);
  const startsWithFilterRef = useRef();

  const backdropClicked = (e) => {
    if (!startsWithFilterRef.current.contains(e.target)) {
      props.backdropClicked(props.component);
      setShowOptions(false);
    }
  };

  const toggleOptions = () => {
    setShowOptions(!showOptions);
  };

  const selectOption = (option) => {
    setSelectedOption(option);
    setShowOptions(false);
    if (numberInput !== '') {
      setDisableFilterButton(false);
    }
  };

  const bindInput = (e) => {
    setDisableFilterButton(false);
    if (e.target.value.length <= 7) {
      setNumberInput(e.target.value);
    }
    if (selectedOption.optionId === options[0].optionId) {
      selectOption(options[0]);
    }
  };

  const changeToDefaultForm = () => {
    setSelectedOption(options[0]);
    setNumberInput('');
    setDisableFilterButton(true);
  };

  const setTemporaryFilterModel = (action) => {
    const model = JSON.parse(JSON.stringify(props.filterModel));
    if (action === 'Filter') {
      if (props.component === 'Weighted') {
        model.weightedFilter = selectedOption.optionId;
        model.weightedFilterValue = numberInput;
        model.useCache = true;
      }
      if (props.component === 'ID') {
        model.idFilter = selectedOption.optionId;
        model.idFilterValue = numberInput;
        model.useCache = true;
      }
    } else {
      if (props.component === 'Weighted') {
        model.weightedFilter = undefined;
        model.weightedFilterValue = undefined;
        model.useCache = true;
      }
      if (props.component === 'ID') {
        model.idFilter = undefined;
        model.idFilterValue = undefined;
        model.useCache = true;
      }
    }

    return model;
  };

  const onClickedButtonFilter = () => {
    props.onClickedButtonFilter(setTemporaryFilterModel('Filter'));
    props.toggleDropdown(null);
    setDisableFilterButton(true);
  };

  const onClickedButtonClearFilter = () => {
    changeToDefaultForm();
    props.onClickedButtonFilter(setTemporaryFilterModel('Clear'));
    props.toggleDropdown(null);
  };

  useImperativeHandle(ref, () => ({
    clearFilterModel() {
      changeToDefaultForm();
    },
  }));

  useEffect(() => {
    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  });

  return (
    <div className="p-3" ref={startsWithFilterRef}>
      <div />
      <div className="position-relative">
        <button
          className={styles.startsWithButton}
          onClick={toggleOptions}
          type="button"
        >
          {selectedOption.optionText}
        </button>
        <div
          className={styles.options}
          style={{
            display: showOptions ? null : 'none',
            marginBottom: `-${25 * options.length}px`,
            height: `${25.25 * options.length}px`,
          }}
        >
          {options.map((value) => (
            <div
              className={styles.optionItem}
              key={value.id}
              onClick={() => selectOption(value)}
              onKeyDown={() => selectOption(value)}
              role="presentation"
            >
              <span>{value.optionText}</span>
            </div>
          ))}
        </div>
      </div>
      <br />
      <input
        className={styles.numberInput}
        type="number"
        value={numberInput}
        placeholder="Enter the value"
        onChange={bindInput}
        onKeyDown={(evt) => (evt.key === 'e' || evt.key === '+' || evt.key === '-')
          && evt.preventDefault()}
      />
      <hr style={{ backgroundColor: '#6d6d73' }} />
      <div className="d-flex">
        <p
          className={styles.clearFilterButton}
          onClick={onClickedButtonClearFilter}
          onKeyDown={onClickedButtonClearFilter}
          role="presentation"
        >
          Clear
        </p>
        <button
          className={styles.filterButton}
          onClick={onClickedButtonFilter}
          disabled={disableFilterButton}
          type="button"
        >
          Filter
        </button>
      </div>
    </div>
  );
});

NumberFilterComponent.propTypes = {
  backdropClicked: PropTypes.func.isRequired,
  component: PropTypes.string,
  onClickedButtonFilter: PropTypes.func.isRequired,
  toggleDropdown: PropTypes.func.isRequired,
  filterModel: PropTypes.shape.isRequired,
};
NumberFilterComponent.defaultProps = {
  component: '',
};
NumberFilterComponent.displayName = 'NumberFilterComponent';
export default NumberFilterComponent;
