import React from 'react';
import PropTypes from 'prop-types';
import viewIcon from '../../assets/icons/Schedule-List.svg';
import Styles from './Submission.module.css';

function SubmissionDataComponent({
  submissions,
  callbackSelectSubmissions,
  isLoading,
}) {
  const selectSubmission = (data) => {
    callbackSelectSubmissions(data);
  };

  const noSubmissionsContent = () => {
    if (isLoading) {
      return (
        <div
          className={`${Styles.loadingHeight}`}
          style={{
            fontSize: '50px',
            color: '#13ad83',
            backgroundColor: 'transparent',
            position: 'relative',
          }}
        >
          <i
            className="fas fa-circle-notch fa-spin mr-1"
            style={{
              top: 0,
              bottom: 0,
              left: 0,
              right: 0,
              margin: 'auto',
              position: 'absolute',
              height: '50px',
              width: '50px',
            }}
          />
        </div>
      );
    }
    return (
      <tr className={`${Styles.loadingHeight}`}>
        <td
          colSpan="7"
          style={{
            fontSize: '50px',
            color: '#13ad83',
            textAlign: 'center',
            backgroundColor: 'transparent',
          }}
        >
          No Submitted Schedules Found
        </td>
      </tr>
    );
  };

  const hasSubmissionsContent = () => {
    if (isLoading) {
      return (
        <div
          className={`${Styles.loadingHeight}`}
          style={{
            fontSize: '50px',
            color: '#13ad83',
            backgroundColor: 'transparent',
            position: 'relative',
          }}
        >
          <i
            className="fas fa-circle-notch fa-spin mr-1"
            style={{
              top: 0,
              bottom: 0,
              left: 0,
              right: 0,
              margin: 'auto',
              position: 'absolute',
              height: '50px',
              width: '50px',
            }}
          />
        </div>
      );
    }
    return (
      <>
        {submissions.map((data) => (
          <tr key={data.id}>
            <td
              className="table-body-column"
              style={{ textIndent: '25px' }}
              data-toggle="tooltip"
              data-placement="top"
              title={data.channel}
            >
              {data.channel}
            </td>
            <td
              className="table-body-column"
              style={{ textIndent: '25px' }}
              data-toggle="tooltip"
              data-placement="top"
              title={data.period}
            >
              {data.period}
            </td>
            <td
              className="table-body-column"
              style={{ textIndent: '50px' }}
              data-toggle="tooltip"
              data-placement="top"
              title={data.weighted}
            >
              {data.weighted}
            </td>
            <td
              className="table-body-column"
              style={{ textIndent: '33px' }}
              data-toggle="tooltip"
              data-placement="top"
              title={data.id}
            >
              {data.id}
            </td>
            <td className="table-body-column" style={{ textIndent: '70px' }}>
              <img
                name={data.id}
                style={{ cursor: 'pointer', marginLeft: '-25px' }}
                src={viewIcon}
                alt="ViewIcon"
                onClick={() => selectSubmission(data)}
                onKeyDown={() => selectSubmission(data)}
                role="presentation"
                data-toggle="tooltip"
                data-placement="top"
                title="View schedule"
              />
            </td>
          </tr>
        ))}
      </>
    );
  };

  return (
    <>
      {submissions.length === 0 && noSubmissionsContent()}
      {submissions.length > 0 && hasSubmissionsContent()}
    </>
  );
}
SubmissionDataComponent.propTypes = {
  submissions: PropTypes.arrayOf(PropTypes.shape).isRequired,
  callbackSelectSubmissions: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
};
SubmissionDataComponent.defaultProps = {
  isLoading: true,
};
export default SubmissionDataComponent;
