import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import Calendar from 'react-calendar';

import styles from './date.filter.module.css';
import './react-calendar.css';
import arrowLeft from '../../../../../assets/icons/Arrow-Left.png';

const { forwardRef, useImperativeHandle } = React;
const DateFilterComponent = forwardRef((props, ref) => {
  const monthNames = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sept',
    'Oct',
    'Nov',
    'Dec',
  ];
  const options = [
    {
      optionId: 1,
      optionName: '1 Month',
    },
    {
      optionId: 2,
      optionName: '2 Months',
    },
    {
      optionId: 3,
      optionName: '3 Months',
    },
    {
      optionId: 4,
      optionName: 'Custom',
    },
  ];
  const dateTodayString = `${
    monthNames[new Date().getMonth()]
  } ${new Date().getDate()}, ${new Date().getFullYear()}`;
  const [id, setId] = useState(4);
  const [startDate, setStartDate] = useState(new Date());
  const [startDateString, setStartDateString] = useState(dateTodayString);
  const [endDate, setEndDate] = useState(new Date());
  const [endDateString, setEndDateString] = useState(dateTodayString);
  const [filterId, setFilterId] = useState(4);
  const [totalDays, setTotalDays] = useState();
  const dateFilterRef = useRef();
  const [startDateData, setStartDateData] = useState();
  const [endDateData, setEndDateData] = useState();

  const backdropClicked = (e) => {
    if (!dateFilterRef.current.contains(e.target)) {
      props.backdropClicked(props.component);
    }
  };

  const calculateTotalDaysRange = (start, end) => {
    const miliSecondsPerDay = 86400000;
    const timeDifference = end.getTime() - start.getTime();
    const totalDaysComputed = Math.round(timeDifference / miliSecondsPerDay);
    setTotalDays(totalDaysComputed);
  };

  const selectDayRange = (dayID) => {
    setId(dayID);
    setStartDateData();
    setEndDateData();

    switch (dayID) {
      case 1: {
        const oneMonthAgo = new Date();
        oneMonthAgo.setMonth(oneMonthAgo.getMonth() - 1);
        setStartDate(oneMonthAgo);
        calculateTotalDaysRange(oneMonthAgo, new Date());
        setStartDateString(
          `${
            monthNames[oneMonthAgo.getMonth()]
          } ${oneMonthAgo.getDate()}, ${oneMonthAgo.getFullYear()}`
        );
        setFilterId(1);
        break;
      }
      case 2: {
        const twoMonthsAgo = new Date();
        twoMonthsAgo.setMonth(twoMonthsAgo.getMonth() - 2);
        setStartDate(twoMonthsAgo);
        calculateTotalDaysRange(twoMonthsAgo, new Date());
        setStartDateString(
          `${
            monthNames[twoMonthsAgo.getMonth()]
          } ${twoMonthsAgo.getDate()}, ${twoMonthsAgo.getFullYear()}`
        );
        setFilterId(2);
        break;
      }
      case 3: {
        const threeMonthsAgo = new Date();
        threeMonthsAgo.setMonth(threeMonthsAgo.getMonth() - 3);
        setStartDate(threeMonthsAgo);
        calculateTotalDaysRange(threeMonthsAgo, new Date());
        setStartDateString(
          `${
            monthNames[threeDaysAgo.getMonth()]
          } ${threeMonthsAgo.getDate()}, ${threeMonthsAgo.getFullYear()}`
        );
        setFilterId(3);
        break;
      }
      default: {
        setStartDate(new Date());
        setStartDateData(new Date());
        calculateTotalDaysRange(new Date(), new Date());
        setStartDateString(
          `${
            monthNames[new Date().getMonth()]
          } ${new Date().getDate()}, ${new Date().getFullYear()}`
        );
        setFilterId(4);
        break;
      }
    }
    setEndDateString(
      `${
        monthNames[new Date().getMonth()]
      } ${new Date().getDate()}, ${new Date().getFullYear()}`
    );
    setEndDate(new Date());
    setEndDateData(new Date());
  };

  const selectStartDate = (day) => {
    setStartDate(day);
    setStartDateData(day);
    setStartDateString(
      `${monthNames[day.getMonth()]} ${day.getDate()}, ${day.getFullYear()}`
    );
    calculateTotalDaysRange(day, endDate);
  };

  const selectEndDate = (day) => {
    setEndDate(day);
    setEndDateData(day);
    setEndDateString(
      `${monthNames[day.getMonth()]} ${day.getDate()}, ${day.getFullYear()}`
    );
    calculateTotalDaysRange(startDate, day);
  };

  const changeToDefaultForm = () => {
    setStartDate(new Date());
    setEndDate(new Date());
    selectDayRange(1);
    setStartDateData();
    setEndDateData();
  };

  const setTemporaryFilterModel = (action) => {
    const model = JSON.parse(JSON.stringify(props.filterModel));

    if (action === 'Filter') {
      const startData = startDateData;
      const endData = endDateData;
      if (startData != null) startData.setDate(startData.getDate() + 1);
      if (endData != null) endData.setDate(endData.getDate() + 1);

      const start = startData != null ? startData : startDate;
      const end = endData != null ? endData : endDate;

      if (props.component === 'Period') {
        model.periodFilter = filterId;
        model.periodFilterStartRange = start;
        model.periodFilterEndRange = end;
        model.useCache = true;
      }
    } else if (props.component === 'Period') {
      model.periodFilter = undefined;
      model.periodFilterStartRange = undefined;
      model.periodFilterEndRange = undefined;
      model.useCache = true;
    }
    return model;
  };

  const onClickedButtonClearFilter = () => {
    changeToDefaultForm();
    props.onClickedButtonFilter(setTemporaryFilterModel('Clear'));
    props.toggleDropdown(null);
  };

  const onClickedButtonFilter = () => {
    props.onClickedButtonFilter(setTemporaryFilterModel('Filter'));
    props.toggleDropdown(null);
  };

  useImperativeHandle(ref, () => ({
    clearFilterModel() {
      changeToDefaultForm();
    },
  }));

  useEffect(() => {
    selectDayRange(1);

    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="d-flex" style={{ height: '100%' }} ref={dateFilterRef}>
      <div className={styles.options}>
        {options.map((value) => (
          <div
            className={`${styles.optionItem} ${
              id === value.optionId ? styles.activeItem : null
            }`}
            key={value.id}
            onClick={() => selectDayRange(value.optionId)}
            onKeyDown={() => selectDayRange(value.optionId)}
            role="presentation"
          >
            {value.optionName}
          </div>
        ))}
      </div>
      <div className={styles.dateSection}>
        <div className={styles.dateRangeDiv}>
          <p className={styles.dateRangeString}>
            {startDateString}{' '}
            <span>
              <img
                className={styles.arrowLeftIcon}
                src={arrowLeft}
                alt="ArrowLeftIcon"
              />
            </span>{' '}
            {endDateString}
          </p>
        </div>
        <div className={styles.totalDays}>
          {totalDays} Day(
          <small
            style={{ fontSize: '9px', marginLeft: '1px', marginRight: '1px' }}
          >
            s
          </small>
          )
        </div>
        <div className="d-flex">
          <div
            className={styles.startSection}
            style={{
              pointerEvents: id !== 4 ? 'none' : null,
              textAlign: 'center',
            }}
          >
            <Calendar
              tileClassName={({ date }) =>
                date > startDate && date <= endDate
                  ? 'react-calendar__month-view__days__day_between-day-range'
                  : null
              }
              onChange={setStartDate}
              onClickDay={selectStartDate}
              value={startDate}
              maxDate={endDate}
            />
          </div>
          <div className={styles.divider} />
          <div
            className={styles.endSection}
            style={{
              pointerEvents: id !== 4 ? 'none' : null,
              textAlign: 'center',
            }}
          >
            <Calendar
              tileClassName={({ date }) =>
                date >= startDate && date < endDate
                  ? 'react-calendar__month-view__days__day_between-day-range'
                  : null
              }
              onChange={setEndDate}
              onClickDay={selectEndDate}
              value={endDate}
              minDate={startDate}
            />
          </div>
        </div>
        <div
          className="d-flex"
          style={{ marginTop: '-10px', marginLeft: '500px' }}
        >
          <p
            className={styles.clearFilterButton}
            onClick={onClickedButtonClearFilter}
            onKeyDown={onClickedButtonClearFilter}
            role="presentation"
          >
            Clear
          </p>
          <button
            className={styles.applyFilterButton}
            onClick={onClickedButtonFilter}
            type="button"
          >
            Apply
          </button>
        </div>
      </div>
    </div>
  );
});

DateFilterComponent.propTypes = {
  backdropClicked: PropTypes.func.isRequired,
  component: PropTypes.string,
  onClickedButtonFilter: PropTypes.func.isRequired,
  toggleDropdown: PropTypes.func.isRequired,
  filterModel: PropTypes.shape.isRequired,
};
DateFilterComponent.defaultProps = {
  component: '',
};
DateFilterComponent.displayName = 'DateFilterComponent';
export default DateFilterComponent;
